function formatZodIssue(issue: ZodIssue) {
  const path = issue.path.join('.');

  if (!path) {
    return `- ${issue.message}`;
  }

  return `- \`${path}\`: ${issue.message}`;
}

function formatFieldViolations(violation: {
  field: string;
  description: string;
}) {
  return `- \`${violation.field}\`: ${violation.description}`;
}
